import React, { useState } from "react";
import { Modal, Button } from "antd";
import ModalTop from "./ModalTop";
import "./_style.scss";
import ModalBottom from "./ModalBottom";

export default function InstallmentModal() {
  const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [modalText, setModalText] = useState("Content of the modal");

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = () => {
    setModalText("The modal will be closed after two seconds");
    setConfirmLoading(true);
    setTimeout(() => {
      setVisible(false);
      setConfirmLoading(false);
    }, 2000);
  };

  const handleCancel = () => {
    console.log("Clicked cancel button");
    setVisible(false);
  };
  return (
    <>
      <Button type="primary" onClick={showModal}>
        Open Modal with async logic
      </Button>
      <Modal
        footer={null}
        className="installment_modal"
        title={
          <p className="installment_modal_title title20_bold">
            Купить в рассрочку
          </p>
        }
        visible={visible}
        onOk={handleOk}
        confirmLoading={confirmLoading}
        onCancel={handleCancel}
      >
        <ModalTop />
        <ModalBottom />
        <div className="installment_Modal_buttons">
          <button className="modal_continue_purchase" onClick={handleCancel}>
            Продолжить покупки
          </button>
          <button className="modal_make_purchase" onClick={handleOk}>
            Оформить покупку
          </button>
        </div>
      </Modal>
    </>
  );
}
